<template>
  <span class="text-xs ui-status" :class="statusClassName">
    <strong>{{ status.toUpperCase() }}</strong>
  </span>
</template>

<script>
export default {
  name: "UiStatus",
  props: {
    status: {
      type: String,
      required: true,
    },
  },
  computed: {
    statusClassName() {
      return `--${this.status.toLowerCase()}`;
    },
  },
};
</script>

<style scoped>
.ui-status {
  display: block;
  width: 80px;
  padding: 5px 0;
  line-height: 1em;
  text-align: center;
  font-size: 12px;
}

.ui-status strong {
  color: #fff;
}

.ui-status.--in_progress {
  background-color: #ffa300;
}

.ui-status.--ready {
  background-color: #00a3e0;
}

.ui-status.--pending {
  background-color: #c4c4c7;
}

.ui-status.--failed {
  background-color: #e41932;
}

.ui-status.--pushed {
  background-color: #3bc14a;
}

.ui-status.--pushing {
  background-color: #ffa300;
}
</style>
